/* eslint-disable no-console */
<template>
    <Layout>
        <PageHeader :title="title" :items="items" />
        <div class="row">
            <div class="col-lg-12">
                <div class="card">
                    <div class="card-body">
                        <h4 class="card-title mb-4">Create New Department</h4>
                        <form>
                            <div class="form-group row mb-4">
                                <label
                                    for="projectname"
                                    class="col-form-label col-lg-2"
                                >Department Name</label>
                                <div class="col-lg-10">
                                    <input
                                        id="projectname"
                                        name="projectname"
                                        type="text"
                                        class="form-control"
                                        placeholder="Enter Project Name..."
                                        v-model="name"
                                    />
                                </div>
                            </div>
                            <div class="form-group row mb-4">
                                <label
                                    for="projectdesc"
                                    class="col-form-label col-lg-2"
                                >Department Description</label>
                                <div class="col-lg-10">
                                    <textarea
                                        id="projectdesc"
                                        class="form-control"
                                        rows="3"
                                        placeholder="Enter Project Description..."
                                        v-model="description"
                                    ></textarea>
                                </div>
                            </div>

                            <div class="form-group row mb-4">
                                <label
                                    for="projectname"
                                    class="col-form-label col-lg-2"
                                >Department Head</label>
                                <div class="col-lg-10">
                                    <input
                                        id="projectname"
                                        name="projectname"
                                        type="text"
                                        class="form-control"
                                        placeholder="Search employees"
                                        v-model="head"
                                    />
                                </div>
                            </div>

                            <div class="form-group row mb-4">
                                <label
                                    for="projectbudget"
                                    class="col-form-label col-lg-2"
                                >Monthly Budget</label>
                                <div class="col-lg-10">
                                    <input
                                        id="projectbudget"
                                        name="projectbudget"
                                        type="text"
                                        placeholder="Enter Project Budget..."
                                        class="form-control"
                                        v-model="budget"
                                    />
                                </div>
                            </div>
                        </form>

                        <div class="row justify-content-end">
                            <div class="col-lg-10">
                                <button
                                    type="submit"
                                    @click="handleNewDepartmentCreation"
                                    class="btn btn-primary"
                                >Create Department</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!-- end row -->
    </Layout>
</template>

<script>
// import DatePicker from "vue2-datepicker";

import Layout from "../../layouts/main";
import PageHeader from "@/components/page-header";
import appConfig from "@/app.config";
import axios from "axios";
// import * as http from "http";
/**
 * Projects-create component
 */
export default {
    page: {
        title: "Create New Department",
        meta: [{ name: "description", content: appConfig.description }]
    },
    components: { Layout, PageHeader },
    data() {
        return {
            title: "Create New",
            items: [
                {
                    text: "Departments",
                    href: "/"
                },
                {
                    text: "Create New",
                    active: true
                }
            ],
            dropzoneOptions: {
                url: "https://httpbin.org/post",
                thumbnailWidth: 150,
                maxFilesize: 0.5,
                headers: { "My-Awesome-Header": "header value" }
            },
            daterange: "",
            name: "",
            description: "",
            budget: "",
            head: ""
        };
    },
    methods: {
        handleNewDepartmentCreation() {
            let error = [];
            if (this.name == "") {
                error.push("name is required");
            }
            if (this.description == "") {
                error.push("description is required");
            }

            if (error.length < 1) {
                let data = {
                    name: this.name,
                    desc: this.description,
                    budget: this.budget,
                    head: this.head
                };

                // axios method of api call
                axios({
                    method: "POST",
                    url: "http://localhost:3000/create-department",
                    data: data,
                    headers: {
                        "content-type": "application/json",
                    },
                    crossDomain:true,
                })
                    .then(resp => {
                        alert(resp);
                    })
                    .catch(err => {
                        alert(err);
                    });

                // fetch API used for remote api call
                // fetch('http://localhost:3000/create-department', {
                //   method: 'POST',
                //   headers: {
                //     "Content-type": "application/json",
                //     "Access-Control-Allow-Origin":"http://localhost:3000"
                //   },
                //   mode: 'no-cors',
                //   body: {test:"test",...data},
                // })
                // .then(function json(response) {
                //   return response.json()
                // })
                // .then(function (data) {
                //   alert('Request succeeded with JSON response', data);
                // })
                // .catch(function (error) {
                //   alert('Request failed', error);
                // });

                // native http for remote api call
                // http.request({
                //   url: "https://localhost:3000/create-department",
                //   method: "POST",
                //   headers: { "Content-Type": "application/json" },
                //   content: JSON.stringify(data)
                // }).then(response => {
                //   var result = response.content.toJSON();
                //   // eslint-disable-next-line no-console
                //   console.log(result)
                // }, error => {
                //   // eslint-disable-next-line no-console
                //   console.error(error);
                // });

                // Using XMLHttpRequest
                // var xmlhttp = new XMLHttpRequest(); // new HttpRequest instance
                // var theUrl = "https://localhost:3000/create-department";
                // xmlhttp.open("POST", theUrl);
                // xmlhttp.setRequestHeader(
                //     "Content-Type",
                //     "application/json;charset=UTF-8"
                // );
                // xmlhttp.send(
                //     JSON.stringify(data)
                // );
            } else {
                alert(error);
            }
        }
    }
};
</script>
